.input-container {
	display: inline-block;
	position: relative;
	height: fit-content;
	padding-top: 1em;
	margin-top: -1em;
	/* max-width: 253px; */
	min-width: 150px;
}
.input-container > .input, .input-container{
	width: 100%;
}
.input {
	border: none;
	padding: 8px;
	border-radius: 8px;
	background-color: #eeeeee;
}
.input + label {
	left: 10%;
	bottom: 9px;
	position: absolute;
	cursor: text;
	color: var(--gray);
	transition: 0.3s cubic-bezier(0.23, 0.72, 0.28, 1.4);
	transition-property: transform, color;

	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	width: 80%;
}
.input:focus + label, .input-container.filled label{
	transform: translateY(-140%) scale(.8);
	color: black;
}