@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;400;500;600;700&display=swap);
.wrapper{
	overflow: hidden;
	z-index: 2;
}
.wrapper__blur-background {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: red center var(--nav-background-pos-v);
	background: var(--nav-background, red) center var(--nav-background-pos-v);
	background-size: cover;
	/* background-attachment: fixed; */
	-webkit-filter: blur(4px);
	        filter: blur(4px);
	z-index: -1;
}
.cart__ico{
	padding: 10px;
	border-radius: 100%;
	overflow: visible;
	box-sizing: content-box;
	background-color: hsl(120, 56%, 86%);
	margin-bottom: var(--price-margin);
}
.cart{
	/* position: absolute; */
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	--price-margin: 2px;
	font-size: .8rem;
	--popup-hover:1.2;
	--popup-active:1.3;
}
.cart>a{
	position: relative;
}
.cart__counter{
	position: absolute;
	background-color: yellow;
	vertical-align: middle;
	bottom: var(--price-margin);
	left: -5px;
	width: 1.5em;
	line-height: 1.5em;
	/* padding: 10px; */
	overflow: visible;
	border-radius: 100%;
	/* width: 30px; */
	/* height: 30px; */
	/* padding: 5px; */
}
.cart__price{
	font-size: .8em;
}
.input-container {
	display: inline-block;
	position: relative;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	padding-top: 1em;
	margin-top: -1em;
	/* max-width: 253px; */
	min-width: 150px;
}
.input-container > .input, .input-container{
	width: 100%;
}
.input {
	border: none;
	padding: 8px;
	border-radius: 8px;
	background-color: #eeeeee;
}
.input + label {
	left: 10%;
	bottom: 9px;
	position: absolute;
	cursor: text;
	color: var(--gray);
	transition: 0.3s cubic-bezier(0.23, 0.72, 0.28, 1.4);
	transition-property: color, -webkit-transform;
	transition-property: transform, color;
	transition-property: transform, color, -webkit-transform;

	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	width: 80%;
}
.input:focus + label, .input-container.filled label{
	-webkit-transform: translateY(-140%) scale(.8);
	        transform: translateY(-140%) scale(.8);
	color: black;
}
.gallery {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: center;
	        justify-content: center;
	grid-column-gap: 30px;
	-webkit-column-gap: 30px;
	        column-gap: 30px;
	grid-row-gap: 10px;
	row-gap: 10px;
}
.product {
	padding: 5px 30px;
	font-size: 0.7em;
	width: 100%;
	--popup-hover: 1.05;
	--popup-focus: var(--popup-hover);
	/* min-width: 300px; */
	/* max-width: 329px; */
}
.product h1 {
	height: 2.5em;
}
.product__img {
	height: auto;
	object-fit: cover;
	-webkit-flex-grow: 3;
	        flex-grow: 3;
	min-width: 100px;
	margin-bottom: 20px;
}
.product__content {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	grid-gap: 20px;
	gap: 20px;
	margin: 0 -10px;
}
.product aside {
	min-width: 120px;
	box-sizing: content-box;
	font-size: 1rem;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
}
.product__btn {
	margin-top: 15px;
	--popup-hover: 1.2;
	--popup-focus: 1;
	--popup-active: 1.2;
}
.product__btn:hover {
	--popup-hover: 1.3;
	--popup-active: 1.1;
}
@media screen and (min-width: 600px) {
	.product__content {
		-webkit-flex-direction: column;
		        flex-direction: column;
		height: calc(100%);
	}
	.product {
		width: 285px;
		height: 500px;
		height: -webkit-fit-content;
		height: -moz-fit-content;
		height: fit-content;
	}
	.product__img {
		width: 100%;
	}
	.gallery {
		margin: 0 6%;
		grid-row-gap: 130px;
		row-gap: 130px;
	}
}

.header {
	--nav-background: url(../../static/media/header_background.ec18e194.jpg);
	--nav-background-pos-v: 53%;

	padding-top: 43px;
	background: red center var(--nav-background-pos-v), var(--light-green);
	background: var(--nav-background, red) center var(--nav-background-pos-v), var(--light-green);
	background-size: cover;
	width: 100%;
	height: 240px;
	position: relative;
	margin-bottom: 60px;
}
.header__wrapper {
	position: relative;
	box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.432);
}
.wrapper__nav.white-overlay::before {
	z-index: -1;
}
.wrapper__nav {
	padding: 20px 15px 20px;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	grid-gap: 25px;
	gap: 25px;
	/* box-shadow: inset 0 5px 5px 0 white; */
	background: linear-gradient(180deg, rgba(255, 255, 255, 0.548) 0%, transparent 35%);
}
.flex {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}
.nav__flex-top,
.nav__flex-bottom {
	width: 80%;
}
.nav__flex-top {
	grid-gap: 80px;
	gap: 80px;
}
.nav__flex-bottom {
	-webkit-justify-content: center;
	        justify-content: center;
	grid-gap: 20px;
	gap: 20px;
}

.nav__logo {
	width: 75px;
	min-width: 75px;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
}
.logo__svg {
	overflow: visible;
}
.nav__burger {
	z-index: 10;
	background: none;
	border: none;
	cursor: pointer;
}
/***** animations ******/

