.nav__logo:hover .outer-circle {
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
	opacity: 0.4;
}
.header__input-container {
	display: none;
	margin: 20px auto;
	max-width: 531px;
	/* margin-top: 20px; */
}
.header__input-container input:focus + label,
.header__input-container.filled label {
	color: white;
}
@media (pointer: fine) {
	/* stagger all */
	.nav__menu-item,
	.nav__logo,
	.nav__cart,
	.header .input,
	.nav__burger {
		-webkit-animation: 0.7s appear-opacity both var(--jump-end-bezier);
		        animation: 0.7s appear-opacity both var(--jump-end-bezier);
	}
	.nav__menu-item,
	.header .input {
		-webkit-animation-name: appear-opacity, appear-scale;
		        animation-name: appear-opacity, appear-scale;
		-webkit-animation-duration: 0.7s, 0.3s;
		        animation-duration: 0.7s, 0.3s;
	}
	/* prettier ignore */
	.nav__logo {
		-webkit-animation-delay: 0.1s;
		        animation-delay: 0.1s;
	}
	/* prettier ignore */
	.nav__cart {
		-webkit-animation-delay: 0.2s;
		        animation-delay: 0.2s;
	}
	/* prettier ignore */
	.header .input-container {
		-webkit-animation-delay: 0.4s;
		        animation-delay: 0.4s;
	}
}
/* RWD */
@media screen and (min-width: 1200px) {
	.nav__logo{
		width: 110px;
		position: absolute;
		top: 50%;
		left: var(--top-padding-sides);
		-webkit-transform: translate(-50%, -50%);
		        transform: translate(-50%, -50%);
	}
	.nav__cart{
		position: absolute;
		top: 20px;
		right: var(--top-padding-sides);
	}
}
@media screen and (min-width: 600px) {
	/* .nav__flex-top {
		width: 80%;
		position: absolute;
		top: 20px;
	} */
	.wrapper__nav {
		padding: 20px 20% 30px;
		-webkit-justify-content: flex-end;
		        justify-content: flex-end;
		--top-padding-sides: 200px;
	}
	ul.nav__menu-list {
		display: -webkit-flex;
		display: flex;
		margin: auto;
		width: 80%;
		-webkit-justify-content: space-between;
		        justify-content: space-between;
		overflow: visible;
	}
	/* prettier-ignore */
	.nav__flex-bottom 				{	width: 100%; }
	/* prettier-ignore */
	.nav__input-container,
	.header__menu,
	button.nav__burger 				{	display: none; }
	/* prettier-ignore */
	.header 									{	height: 338px; }
	/* prettier-ignore */
	.header__wrapper 					{	height: 180px; }
	/* prettier-ignore */
	.header__blur-background	{	--nav-background-pos-v:calc(53% + 40px) }
	/* prettier-ignore */
	.header__input-container	{ display: block;	}
}
/* fix cover backgrounds mismatch on 315px breakpoint	 */
@media screen and (max-width: 350px) {
	.header__wrapper > .wrapper__blur-background {
		background-size: auto 133%;
	}
}

.menu-item{
	letter-spacing: 5px;
}

.header__menu {
	width: 40%;
	min-width: 200px;
	position: absolute;
	right: 0;
	bottom: -110px;
	z-index: 10;
	overflow: hidden;
}
.header__menu-item {
	background-color: var(--light-green);
	margin-right: 15%;
	transition: 0.8s opacity var(--jump-end-bezier), 0.5s -webkit-transform var(--jump-both-bezier);
	transition: 0.5s transform var(--jump-both-bezier), 0.8s opacity var(--jump-end-bezier);
	transition: 0.5s transform var(--jump-both-bezier), 0.8s opacity var(--jump-end-bezier), 0.5s -webkit-transform var(--jump-both-bezier);
}

.header__menu-item > a {
	width: 100%;
	padding: 10px 20px;
	display: inline-block;
	overflow: hidden;
}
/* prettier-ignore */
.header__menu-item:not(:last-child)	{	margin-bottom: 2px; }

.nav__menu-list {
	display: none;
	padding: 0;
	font-size: 1.2em;
}

.header__menu--hidden .menu-item {
	opacity: 0;
	-webkit-transform: translateX(100%);
	        transform: translateX(100%);
}
.header__menu--visible .menu-item {
	opacity: 1;
	-webkit-transform: none;
	        transform: none;
	-webkit-transform: initial;
	        transform: initial;
}


:root {
	--light-green: #dff6df;
	--green: hsl(120, 56%, 79%);
	--dark-green: hsl(120, 86%, 29%);
	--darkX-green: #232;
	--gray: #adadad;
	--light-gray: #efefef;
	--overlay: rgba(255, 255, 255, 0.25);
	--overlay-dense: rgba(255, 255, 255, 0.56);

	--quick-bezier: cubic-bezier(0.16, 0.79, 0.72, 1.08);
	--jump-end-bezier: cubic-bezier(0.23, 0.53, 0.71, 1.52);
	--jump-both-bezier: cubic-bezier(0.38, -0.57, 0.56, 1.48);
}

body {
	text-align: center;
	font-family: 'Comfortaa', cursive;
	font-weight: 300;
	letter-spacing: 1.5px;
	--font-color: hsl(0, 0%, 10%);
	/* color: var(--font-color); */
}

/* h1,
h2,
h3,
h4 {
	font-family: 'italiana';
} */
.btn {
	border: none;
	background: none;
	cursor: pointer;
	padding: 15px 10px;
	border-radius: 10px;
}
.btn--green {
	background-color: hsl(120, 56%, 79%);
	background-color: var(--green, red);
}
.white-overlay::before {
	content: '';
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: rgba(255, 255, 255, 0.25);
	background-color: var(--overlay, red);
}
.white-overlay--dense::before {
	background-color: rgba(255, 255, 255, 0.56);
	background-color: var(--overlay-dense, red);
}

ul[class] {
	list-style-type: none;
	margin: 0;
}
a {
	transition: 0.3s color cubic-bezier(0.16, 0.79, 0.72, 1.08);
	transition: 0.3s color var(--quick-bezier);
}

/* ////////////////////// */
/* 				FANCY 					*/

/* Animations */
@-webkit-keyframes appear-scale {
	from {
		-webkit-transform: scale(0);
		        transform: scale(0);
	}
	to {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}
@keyframes appear-scale {
	from {
		-webkit-transform: scale(0);
		        transform: scale(0);
	}
	to {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}
@-webkit-keyframes appear-opacity {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}
@keyframes appear-opacity {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}
@-webkit-keyframes rotate {
	from {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
@keyframes rotate {
	from {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
@media screen and (min-width: 600px) {
	/* underline */
	.double-underline {
		position: relative;
		--underline-height: 1px;
	}
	.double-underline::before,
	.double-underline::after {
		content: '';
		position: absolute;
		-webkit-transform: scale(0);
		        transform: scale(0);
		transition: 0.3s opacity cubic-bezier(0.16, 0.79, 0.72, 1.08), 0.3s -webkit-transform cubic-bezier(0.23, 0.53, 0.71, 1.52);
		transition: 0.3s transform cubic-bezier(0.23, 0.53, 0.71, 1.52), 0.3s opacity cubic-bezier(0.16, 0.79, 0.72, 1.08);
		transition: 0.3s transform cubic-bezier(0.23, 0.53, 0.71, 1.52), 0.3s opacity cubic-bezier(0.16, 0.79, 0.72, 1.08), 0.3s -webkit-transform cubic-bezier(0.23, 0.53, 0.71, 1.52);
		transition: 0.3s opacity var(--quick-bezier), 0.3s -webkit-transform var(--jump-end-bezier);
		transition: 0.3s transform var(--jump-end-bezier), 0.3s opacity var(--quick-bezier);
		transition: 0.3s transform var(--jump-end-bezier), 0.3s opacity var(--quick-bezier), 0.3s -webkit-transform var(--jump-end-bezier);
		opacity: 0;
		background-color: black;
		background-color: var(--font-color, black);
		height: 2px;
		height: var(--underline-height, 2px);
		width: 100%;
		bottom: -3px;
	}
	.double-underline::after {
		bottom: -7px;
		left: 0;
		-webkit-transform-origin: right;
		        transform-origin: right;
	}
	.double-underline:hover::before,
	.double-underline:hover::after {
		-webkit-transform: scaleX(1.2);
		        transform: scaleX(1.2);
		opacity: 1;
	}
	/* popup */
	.popup,
	.popup-catch .popup-receive {
		transition: 0.25s -webkit-transform cubic-bezier(0.23, 0.53, 0.71, 1.52);
		transition: 0.25s transform cubic-bezier(0.23, 0.53, 0.71, 1.52);
		transition: 0.25s transform cubic-bezier(0.23, 0.53, 0.71, 1.52), 0.25s -webkit-transform cubic-bezier(0.23, 0.53, 0.71, 1.52);
		transition: 0.25s -webkit-transform var(--jump-end-bezier);
		transition: 0.25s transform var(--jump-end-bezier);
		transition: 0.25s transform var(--jump-end-bezier), 0.25s -webkit-transform var(--jump-end-bezier);
	}
	.popup:hover,
	.popup-catch:hover .popup-receive {
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
		-webkit-transform: scale(var(--popup-hover, 1.1));
		        transform: scale(var(--popup-hover, 1.1));
	}
	.popup:focus,
	.popup-catch:focus .popup-receive {
		-webkit-transform: scale(1.15);
		        transform: scale(1.15);
		-webkit-transform: scale(var(--popup-focus, 1.15));
		        transform: scale(var(--popup-focus, 1.15));
	}
	.popup:active,
	.popup-catch:active .popup-receive {
		-webkit-transform: scale(1.15);
		        transform: scale(1.15);
		-webkit-transform: scale(var(--popup-active, var(--popup-focus, 1.15)));
		        transform: scale(var(--popup-active, var(--popup-focus, 1.15)));
	}
}

